import VDistpicker from "v-distpicker";
import axios from "axios";
export default {
  components: { VDistpicker },
  data() {
    return {
      product: "",
      phoneCardType: "",
      name: "",
      phoneNum: "",
      province: "",
      city: "",
      area: "",
      address: "",
      isAccept: false,
      options: [
        {
          value: "移动",
          label: "移动"
        },
        {
          value: "联通",
          label: "联通"
        },
        {
          value: "电信",
          label: "电信"
        }
      ]
    };
  },
  computed: {
    date() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      return `${year}-${month}-${strDate}`;
    }
  },
  mounted() {
    // var date = new Date();
    // var year = date.getFullYear();
    // var month = date.getMonth() + 1;
    // var strDate = date.getDate();
    // console.log(year, month, strDate);
  },
  methods: {
    onChangeProvince(e) {
      console.log(e);
      this.province = e.value;
    },
    onChangeCity(e) {
      console.log(e);
      this.city = e.value;
    },
    onChangeArea(e) {
      console.log(e);
      this.area = e.value;
    },
    gotoReport() {
      this.$router.push("/report");
    },
    async submit() {
      const id = this.$route.query.id;
      const staffId = this.$route.query.staffId;
      const userData = {
        id: id,
        staffId: staffId,
        product: this.product,
        phoneCardType: this.phoneCardType,
        name: this.name,
        phoneNum: this.phoneNum,
        province: this.province,
        city: this.city,
        area: this.area,
        address: this.address,
        isAccept: this.isAccept
      };
      if (this.isAccept) {
        if (
          this.product &&
          this.phoneCardType &&
          this.name &&
          this.phoneNum &&
          this.province &&
          this.city &&
          this.area &&
          this.address
        ) {
          const firstStr = this.phoneNum.substr(0, 1);
          // console.log(firstStr);
          // console.log(this.phoneNum.length);
          if (this.phoneNum.length == 11 && firstStr == "1") {
            const submitRes = await axios.post("/saveUserData", userData);
            if (submitRes.data === "success") {
              this.$message({
                message: "提交成功",
                type: "success"
              });
              location.reload();
            } else {
              this.$message.error("提交失败，一个电话号只能参加一次");
              location.reload();
            }
            console.log(submitRes.data);
          } else {
            alert("手机号码不正确");
          }
        } else {
          alert("含*的内容不能为空");
        }
      } else {
        alert("请接收邮费自理");
      }
    }
  }
};



